@import url(https://fonts.googleapis.com/css2?family=Roboto&display=swap);
body {
  margin: 0;
  padding: 0 5%;
 
  /* background-color: #FFFEF8; */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

html {
  height: 100%;
}

* { /* Safari/Chrome, other WebKit */    /* Firefox, other Gecko */
  box-sizing: border-box;  
}

body,
body * {
  font-family: "Roboto";
}

.App {
  height: 100vh;
}

.filterRoot {
  height: 100%;
  display: flex;
  flex-direction: column;
}

.filterRoot input {
  background: #ebebeb;
  border: 1px solid black;
  border-radius: 2px;
  padding: 5px 5px;
  box-sizing: border-box;
}

.filterRoot form {
  margin: 10px 0px;
}

.filterRoot h4 {
  margin-bottom: 10px;
  margin-top: 10px;
}

.filterRoot div {
  display: flex;
  align-items: center;
  margin: 8px 0px;
}

.filterRoot div p {
  margin: 0;
  font-size: 0.9rem;
}

.filterRoot div svg {
  font-size: 0.9rem;
  margin-left: 10px;
  -ms-grid-row-align: center;
  align-self: center;
  border-radius: 50%;
  padding: 3px;
}

.filterRoot div svg:hover {
  background-color: #ddd;
  cursor: pointer;
}

.filterSubmit {
  display: none;
}

.filterYear {
  display: flex;
  flex-direction: column;
}

.filterRoot button {
  background: none;
  color: #52b788;
  border: none;
  padding: 0;
  font: inherit;
  cursor: pointer;
  outline: inherit;
  margin: auto;
  margin-top: 8px;
  border-radius: 100%;
  margin-bottom: 10px;
}

.filterRoot button:hover {
  background-color: #c8e0d5;
  -webkit-filter: brightness(1.1);
          filter: brightness(1.1);
}

.filterYear div {
  display: flex;
}

.filterYear div label {
  margin-left: 5px;
  font-size: 0.9rem;
}
