.findRoot {
  display: "flex";
}

.profileImgContainer {
  height: 100%;
  overflow: hidden;
  background-color: #b3b3b3;
  border-radius: 10px 10px;
}

.profileImgContainer:after {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: none;
  color: #fff;
  z-index: 999999;
}

.profileImg {
  width: 100%;
  height: 100%;
  object-fit: cover;
  opacity: 0.9;
}

