.itemContainer-rejected {
  height: 100%;
  cursor: pointer;
  box-shadow: 0 0 3px #ddd;
  border-radius: 5px 5px;
  background: rgba(255, 54, 97, 0.5);
}

.itemContainer-accepted {
  height: 100%;
  cursor: pointer;
  box-shadow: 0 0 3px #ddd;
  border-radius: 5px 5px;
  background: rgba(82, 183, 136, 0.8);
}

.itemContainer-lowOpac {
  opacity: 0.5;
}

.itemContainer {
  height: 100%;
  cursor: pointer;
  box-shadow: 0 0 3px #ddd;
  border-radius: 5px 5px;
}

.itemContainer:hover {
  -webkit-animation: containerAnimation 250ms forwards;
          animation: containerAnimation 250ms forwards;
  box-shadow: 0 0 10px #ddd;
}

.itemFooter {
  position: relative;
  z-index: 99;
  /* display: inline; */
