.loginRoot {
  width: 100%;
  height: 80%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.loginForm-root {
  width: 20%;
}

.loginForm-root form {
  width: 100%;
}

.loginForm-input input {
  width: 100%;
  height: 25px;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  border: 1px solid black;
  flex-grow: 1;
  border-radius: 3px;
}

.loginForm-input input:focus {
